import styled from "styled-components";
import { mobile } from "../responsive";

const Container = styled.div`
  width: 100vw;
  height: 100vh;
  background: 
  url("https://images.unsplash.com/photo-1483181957632-8bda974cbc91?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=870&q=80")
  center;
  background-size: cover;

  display: flex;
  align-items: center;
  justify-content: center;
`;


const Wrapper = styled.div`
width: 25%;
  padding: 20px;
background-color: white;
${mobile({width: "75%"})}


`;

const Title = styled.h1`
  font-size: 24px;
  font-weight: 300;
`;

const Form = styled.form`
   display: flex;
   flex-direction: column;
`;



const Input = styled.input`
   flex: 1;
   min-width: 40%;
   margin: 20px  10px 0px 0px;
   padding: 10px;
`;

const Agreement = styled.span`
   font-size: 12px;
   margin: 20px 0px;
`;

const Button = styled.button`
   width: 40%;
   border: none;
   padding: 15px 20px;
   background-color: teal;
   color: white;
   cursor: pointer;
`;


const Register = () => {
  return (
    <Container>
    <Wrapper>
      <Title>CREATE AN ACCOUNT</Title>
      <Form>
        <Input placeholder="name" />
        <Input placeholder="last name" />
        <Input placeholder="username" />
        <Input placeholder="email" />
        <Input placeholder="password" />
        <Input placeholder="confirm password" />
        <Agreement>
          Lorem ipsum, dolor sit amet consectetur adipisicing elit. Eligendi dolores sapiente culpa adipisci, corporis sequi officiis deleniti aperiam voluptatibus veritatis perferendis quis recusandae exercitationem praesentium quam vitae illo quod debitis? <b>PRIVACY POLICY</b>
        </Agreement>
        <Button>CREATE</Button>
      </Form>
      </Wrapper>
    </Container>
  );
};

export default Register;

import React from 'react'

